import React, { Component } from 'react';
import './login.css';

export default class Login extends Component {
    constructor(props) {
        super(props)
        this.state = {
            placeholder: '',
            value: ''
        }
    }
    componentDidMount() {
        document.addEventListener('keydown', (e) => {
            const { socket, isLogin } = this.props
            if (!!this.loginValue && !isLogin) {
                if(e.keyCode == 13) {
                    e.preventDefault()
                    const value = e.target.value
                    if(/[\u4e00-\u9fa5|\w]/g.test(value)) {
                        socket.emit('add user', value);
                        sessionStorage.setItem('username', value)
                        this.loginValue.blur()
                    } else {
                        const placeholder = '只能是中文、数字、字母'
                        const value = ''
                        this.setState(() => ({placeholder, value}))
                    }
                }
            }
        })
    }
    loginChange = (e) => {
        const target = e.srcElement || e.target
        const value = target.value
        let placeholder = this.state.placeholder
        if (!value) {
            placeholder = ''
        }
        this.setState(() => ({value, placeholder}))
    }
    render() {
        const { placeholder, value } = this.state
        const { isLogin } = this.props
        return (
            <div className="login" style={{display: isLogin ? 'none' : 'block'}}>
                <div className="form">
                    <p>请输入你的江湖代号</p>
                    <input type="text" ref={(loginValue) => this.loginValue = loginValue} value={value} onInput={(e) => this.loginChange(e)} placeholder={placeholder} />
                </div>
            </div>
        )
    }
}


