import React, { Component } from 'react';

const Input = (props) => {
    const { changeInput, value, sendUserText } = props
    return (
        <div className="footer">
            <input type="text" id="userInput" placeholder="愉快的聊天..." value={value} onInput={(e) => changeInput(e)}/>
            <span className="send" onClick={() => sendUserText(value)}>发送</span>
        </div>
    )
}
export default Input


