import Input from './Input';
import Count from './Count';
import UserText from './UserText';
import Login from './Login';
import Tip from './Tip';
import OtherText from './OtherText';
import React, { Component } from 'react';
import './App.css';
var socket = require('socket.io-client')('http://192.168.23.3:3077');

class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            value: '',
            isLogin: false,
            num: 0,
            data: []
        }
    }
    componentDidMount() {
        socket.on('login', (d) => {
            this.setState({isLogin: true, num: d.numUsers})
        });
        socket.on('user joined', (d) => {
            let data = this.state.data
            const obj = {
                type: 'tip',
                content: `<i class="red">${d.username}</i> 加入群聊啦，大家快打他！`
            }
            data.push(obj)
            const num = d.numUsers
            this.setState(() => ({data: data, num}))
        });
        socket.on('new message', (d) => {
            let data = this.state.data, type = 'other'
            if (sessionStorage.getItem('username') === d.username) {
                type = 'user'
            }
            const obj = {
                type,
                username: d.username,
                content: d.message
            }
            data.push(obj)
            this.setState(() => ({data: data}))
        });
    }
    changeInput = (e) => {
        const target = e.srcElement || e.target
        const value = target.value
        this.setState(() => ({value}))
    }
    renderText = (data=[]) => {
        return data.map((v, key) => {
            switch (v.type) {
                case "tip":
                    return <Tip key={key} text={v.content} />
                case "user":
                    return <UserText key={key} username={v.username} text={v.content} />
                case "other":
                    return <OtherText key={key} username={v.username} text={v.content} />
                default:
                        break
            }
        })
    }
    sendUserText = (value) => {
        if (!!value) {
            socket.emit('new message', value)
            const data = this.state.data
            const obj = {
                type: 'user',
                username: sessionStorage.getItem('username'),
                content: value
            }
            data.push(obj)
            this.setState(() => ({value: '', data}))
        }
    }
    render() {
        const { value, data, isLogin, num } = this.state
        return [
            <div key="app" className="app clearfix">
                <Count num={num} />
                {this.renderText(data)}
            </div>,
            <Input key="input" changeInput={this.changeInput} value={value} sendUserText={this.sendUserText}  />,
            <Login key="login" socket={socket} isLogin={isLogin} />
        ]
    }
    
}

export default App;

